import {
    ProductController
} from "./product";

// UI Controller
export const UIController = (function (ProductCtrl) {

    const Selectors = {
        productList: '#item-list',
        productListItems: '#item-list tr',
        addButton: '#addBtn',
        productName: '#productName',
        productPrice: '#productPrice',
        productCard: '#productCard',
        totalAzn: '#total-azn',
        totalDollar: '#total-dollar',
        updateBtn: '#updateBtn',
        deleteBtn: '#deleteBtn',
        cancelBtn: '#cancelBtn'

    }

    return {
        createProductList: function (products) {
            let html = '';

            products.forEach(prd => {
                html += `
                    <tr>
                        <td>${prd.id}</td>
                        <td>${prd.name}</td>
                        <td>${prd.price} $</td>
                        <td class="text-end">
                            <button type="submit" class="btn btn-warning btn-sm">
                                <i class="bi bi-pencil-square edit-product"></i>
                            </button>
                        </td>
                    </tr>
                `;
            });

            document.querySelector(Selectors.productList).innerHTML = html;
        },
        getSelectors: function () {
            return Selectors;
        },
        addProduct: function (prd) {
            document.querySelector(Selectors.productCard).style.display = 'block';

            let item = `
                    <tr>
                        <td>${prd.id}</td>
                        <td>${prd.name}</td>
                        <td>${prd.price} $</td>
                        <td class="text-end">
                            <button type="submit" class="btn btn-warning btn-sm">
                                <i class="bi bi-pencil-square edit-product"></i>
                            </button>
                        </td>
                    </tr>
            `;
            document.querySelector(Selectors.productList).innerHTML += item;
        },
        updateProduct: function (prd) {

            let updatedItem = null;

            let items = document.querySelectorAll(Selectors.productListItems);
            items.forEach(item => {
                if (item.classList.contains('bg-warning')) {
                    item.children[1].textContent = prd.name;
                    item.children[2].textContent = prd.price + ' $';
                    updatedItem = item;
                }
            });

            return updatedItem;
        },
        clearInputs: function () {
            document.querySelector(Selectors.productName).value = '';
            document.querySelector(Selectors.productPrice).value = '';
        },
        clearWarnings: function () {
            const items = document.querySelectorAll(Selectors.productListItems);
            items.forEach(item => {
                if (item.classList.contains('bg-warning')) {
                    item.classList.remove('bg-warning');
                }
            });
        },
        hideCard: function () {
            document.querySelector(Selectors.productCard).style.display = 'none';
        },
        showTotal: function (total) {
            document.querySelector(Selectors.totalDollar).textContent = total;
            document.querySelector(Selectors.totalAzn).textContent = parseInt(total * 1.7);
        },
        addProductToForm: function () {
            const selectedProduct = ProductCtrl.getCurrentProduct();

            document.querySelector(Selectors.productName).value = selectedProduct.name;
            document.querySelector(Selectors.productPrice).value = selectedProduct.price;
        },
        deleteProduct: function () {
            const items = document.querySelectorAll(Selectors.productListItems);

            items.forEach(item => {
                if (item.classList.contains('bg-warning')) {
                    item.remove();
                }
            })
        },
        addingState: function () {

            UIController.clearWarnings();

            UIController.clearInputs();

            document.querySelector(Selectors.addButton).style.display = 'inline';
            document.querySelector(Selectors.updateBtn).style.display = 'none';
            document.querySelector(Selectors.deleteBtn).style.display = 'none';
            document.querySelector(Selectors.cancelBtn).style.display = 'none';
        },
        editState: function (tr) {
            tr.classList.add('bg-warning');

            document.querySelector(Selectors.addButton).style.display = 'none';
            document.querySelector(Selectors.updateBtn).style.display = 'inline';
            document.querySelector(Selectors.deleteBtn).style.display = 'inline';
            document.querySelector(Selectors.cancelBtn).style.display = 'inline';
        }
    }
})(ProductController);