import {
    ProductController
} from "./product";
import {
    UIController
} from "./ui";
import {
    StorageController
} from "./storage";

ProductController;
UIController;
StorageController;

// App Controller
const App = (function (UICtrl, ProductCtrl, StorageCtrl) {

    const UISelectors = UICtrl.getSelectors();

    // Load Event Listeners
    const loadEventListeners = function () {

        // add button
        document.querySelector(UISelectors.addButton).addEventListener('click', productAddSubmit);

        // edit product click
        document.querySelector(UISelectors.productList).addEventListener('click', productEditClick);

        // edit product submit
        document.querySelector(UISelectors.updateBtn).addEventListener('click', editProductSubmit);

        // cancel button click
        document.querySelector(UISelectors.cancelBtn).addEventListener('click', cancelButton);

        // delete products submit
        document.querySelector(UISelectors.deleteBtn).addEventListener('click', deleteProductSubmit);
    }

    // product add submit
    const productAddSubmit = function (e) {
        const productName = document.querySelector(UISelectors.productName).value;
        const productPrice = document.querySelector(UISelectors.productPrice).value;

        if (!productName.trim() == 0 && !productPrice.trim() == 0) {

            // add product
            const newProduct = ProductCtrl.addProduct(productName, productPrice);

            // add item to list
            UICtrl.addProduct(newProduct);

            // save product to LS
            StorageCtrl.storeProduct(newProduct);

            // get total
            const total = ProductCtrl.getTotal();

            //  show total
            UICtrl.showTotal(total);

            // clear inputs
            UICtrl.clearInputs();
        }
        e.preventDefault();
    }

    // edit product click
    const productEditClick = function (e) {

        if (e.target.classList.contains('edit-product')) {
            const id = e.target.parentNode.parentNode.previousElementSibling.previousElementSibling.previousElementSibling.textContent;

            // get selected product
            const product = ProductCtrl.getProductById(id);

            // set selected product
            ProductCtrl.setCurrentProduct(product);

            UICtrl.clearWarnings();

            // add product to Form
            UICtrl.addProductToForm();

            UICtrl.editState(e.target.parentNode.parentNode.parentNode);
        }

        e.preventDefault();
    }

    // edit product submit
    const editProductSubmit = function (e) {
        const productName = document.querySelector(UISelectors.productName).value;
        const productPrice = document.querySelector(UISelectors.productPrice).value;

        if (!productName.trim() == 0 && !productPrice.trim() == 0) {

            // update product
            const updateProduct = ProductCtrl.updateProduct(productName, productPrice);

            // update UI
            UICtrl.updateProduct(updateProduct);

            // get total
            const total = ProductCtrl.getTotal();

            //  show total
            UICtrl.showTotal(total);

            // update storage
            StorageCtrl.updateProduct(updateProduct);

            UICtrl.addingState();
        }

        e.preventDefault();
    }

    // cancel button
    const cancelButton = function (e) {


        UICtrl.addingState();
        e.preventDefault();
    }

    // delete product submit
    const deleteProductSubmit = function (e) {

        // get selected product
        const selectedProduct = ProductCtrl.getCurrentProduct();

        // delete product
        ProductCtrl.deleteProduct(selectedProduct);

        // delete ui
        UICtrl.deleteProduct();

        // get total
        const total = ProductCtrl.getTotal();

        // show total
        UICtrl.showTotal(total);

        // delete from storage
        StorageCtrl.deleteProduct(selectedProduct.id);

        UICtrl.addingState();

        if (total == 0) {
            UICtrl.hideCard();
        }

        e.preventDefault();
    }

    return {
        init: function () {
            console.log('starting app...');

            UICtrl.addingState();

            const products = ProductCtrl.getProducts();

            if (products.length == 0) {
                UICtrl.hideCard();
            } else {
                UICtrl.createProductList(products);

            }

            // get total
            const total = ProductCtrl.getTotal();

            // show total
            UICtrl.showTotal(total);

            // load event listeners
            loadEventListeners();

        }
    }
})(UIController, ProductController, StorageController);

App.init();